import Vue from 'vue'
import Router from 'vue-router'
import index from '@/pages/index' //首页
import home from '@/pages/home' //项目

import designDetails from '@/pages/designDetails' //作品详情

Vue.use(Router)

export default new Router({
  routes: [{
      path: '/',
      name: 'index',
      component: index
    },
    {
       path: '/home',
      name: 'home',
      component: home
    }, {
      path: '/designDetails/:projectId',
      name: 'designDetails',
      component: designDetails
    }
  ]
})



