<template>
  <div class="detailsBody">
    <div class="detailsHeader">
      <div class="detailsTop">
        <div class="litterImg" :style="'background-image: url(http://5windy.com'+litterImg+');'"></div>
        <div class="detailsTextBox">
          <div class="textTitle">{{projectData.projectTitle}}</div>
          <div class="dateAndCopyright">{{projectData.projectDate}} ©{{projectData.projectCopyright}}</div>
          <div class="textSynopsis">{{projectData.projectSynopsis}}</div>
        </div>
        <a href="javascript:;">
          <div class="downloadBtn" v-if="projectData.projectCopyright.toLowerCase() != 'windy'">
            <div class="btnIcon"></div>
            <div class="btnText">本地下载</div>
          </div>
        </a>
      </div>
    </div>
    <div class="details">
      <!-- <div><img class="detailsPic" :src="'http://127.0.0.1:5000/static/'+ projectData.projectDetail"></div> -->
      <div class="detailBody" v-html="projectData.projectDetail" style="color:#000!important;"></div>
      <div class="text">如果您觉得本狗做的不错，或者资源对您有用，欢迎任意金额打赏支持</div>
      <div style="display: flex; margin: auto; width: 180px;">
        <a href="javascript:;" @click="showPayBox=!showPayBox">
          <div class="payBtn">赏</div>
        </a>
        <a href="javascript:;" @click="refish">
          <div class="pinlun">评论</div>
        </a>
      </div>
      <transition name="payBox">
        <div v-if="showPayBox">
          <div class="payBox"></div>
          <div class="payText">打赏将支持作者提供更好的服务与更优质的资源</div>
        </div>
      </transition>
      <div id="SOHUCS" :sid="projectId"></div>
    </div>
  </div>
</template>
<script type="text/javascript">
import $ from "jquery"
import changyan from "@/assets/js/changyan.js"
export default {
  //该页面的控制数据
  data() {
    return {
      //首页列表中传来的projectId参数
      // projectId: this.$route.params.projectId,
      projectId: localStorage.getItem("projectId"),

      //是否显示打赏二维码
      showPayBox: false,
      //项目使用的数据`
      projectData: "",
      litterImg: "",
      
    }
  },
  created() {
    //this.showData();
    this.$nextTick(() => {
      this.changyan();
    });
  },
  //父控件传过来的参数
  props: {

  },
  //监听函数
  wacth: {

  },
  //进入页面执行的函数
  mounted() {
    //传入navBar的选择状态
    this.$store.commit('updateNavBarActive', '');
    document.documentElement.scrollTop = 0;
    //this.changyan();
    this.getDesignData();
  },
  //定义函数
  methods: {
    refish() {
      location.reload();
    },
    changyan() {
      window.changyan.api.config({
        appid: 'cyt3crepq',
        conf: 'prod_a13b2e3f57b739f379a9d121e340340d'
      });
    },
    //请求设计详情
    getDesignData() {
      //this.params.touser = this.$store.state.userNameList;
      let params = this.projectId;
      this.$store.dispatch('getDesignData', params).then((response) => {
        let res = response.data;
        if (response.statusText === "OK" && response.status === 200) {
          //console.log(res);
          this.projectData = res;
          this.litterImg = "/static/uploads/" + this.projectData.projectPic;
        } else {
          alert("网络错误")
        }
      }).catch((err) => {
        console.error(err);
      });
    },
    //字符串转HTML
    encodeHtml(str) {
      let encodedStr = "";
      if (str == "") return encodedStr;
      else {
        for (let i = 0; i < str.length; i++) {
          encodedStr += "&#" + str.substring(i, i + 1).charCodeAt().toString(10) + ";";
        }
      }
      return encodedStr;
    },
  },
  //增加控件
  components: {

  }
}

</script>
<style scoped lang="scss">
@import '../assets/sass/public.scss';
.detailsBody {
  padding-top: 80px;
  background-color: $bgColorbai;
  padding-bottom: 20px;
}

.detailBody p {
  // width: 1180px;
  // margin: auto;
  // background-color: $bgColorLight;
  // display: block;
  color: #000;
}

.detailsHeader {
  background-color: #fff;
  width: 100%;
}

.detailsTop {
  min-width: 1024px;
  width: 1180px;
  height: 240px;
  margin: auto;
  display: flex;
  align-items: center;
  position: relative;
}

.litterImg {
  width: 300px;
  height: 200px;
  border-radius: 4px;
  background-color: $bgColorLight;
  background-size: 100%;
}

.detailsTextBox {
  height: 200px;
  margin-left: 30px;
  width: 850px;
  min-width: 400px;
}

.textTitle {
  font-size: 24px;
  color: $bigTitle;
}

.dateAndCopyright {
  font-size: 16px;
  color: $textP;
  margin: 10px 0;
}

.textSynopsis {
  font-size: 16px;
  color: $TextTitle-color;
  font-weight: 400;
}

.downloadBtn {
  width: 160px;
  height: 44px;
  background-color: $blue;
  border-radius: 4px;
  position: absolute;
  top: 20px;
  right: 0;
  display: flex;
  align-items: center;
  justify-content: center;
  transition: all 0.3s ease-out 0s;
}

.downloadBtn:hover {
  background-color: #629DFF;
  transition: all 0.3s ease-out 0s;
}

.btnIcon {
  width: 20px;
  height: 20px;
  background-image: url(../assets/img/downloadIcon.png);
  background-size: 100%;
}

.btnText {
  color: #fff;
  font-size: 16px;
  margin-left: 5px;
}

.details {
  width: 1260px;
  margin: 20px auto 0;
  padding: 40px 0;
  background-color: #fff;
  border-radius: 4px;
  text-align: center;
}

.detailsPic {
  width: 1180px;
  margin: auto;
  background-color: $bgColorLight;
  display: block;
}

.text {
  font-size: 16px;
  color: $textP;
  margin: 30px 0;
}

.payBtn {
  width: 80px;
  height: 80px;
  color: #fff;
  background: #FFB53D;
  font-size: 24px;
  border-radius: 44px;
  text-align: center;
  line-height: 76px;
  transition: all 0.3s ease-out 0s; // background-image: url(../assets/img/payBtn.png);
  // background-size: 100%;
  margin: auto;
}

.pinlun {
  width: 80px;
  height: 80px;
  color: #fff;
  background: #458CFF;
  font-size: 18px;
  border-radius: 44px;
  text-align: center;
  line-height: 76px;
  transition: all 0.3s ease-out 0s; // background-image: url(../assets/img/payBtn.png);
  // background-size: 100%;
  margin-left: 20px;
}

.pinlun:hover {
  background: #186FFF;
  transition: all 0.3s ease-out 0s;
}

.payBox {
  width: 360px;
  height: 220px;
  background-size: 100%;
  background-image: url(../assets/img/payMoney.png);
  margin: 40px auto 10px;
}

.payBtn:hover {
  background: #F7A216;
  transition: all 0.3s ease-out 0s;
}

.payText {
  font-size: 16px;
  color: $textP;
  margin: 20px 0;
}

.payBox-enter-active {
  transition: all .5s ease .2s;
}

.payBox-leave-active {
  transition: all .5s cubic-bezier(1.0, 2, 0.8, 1.0);
  animation: reverse;
}

.payBox-enter,
.payBox-leave-to
/* .slide-fade-leave-active for below version 2.1.8 */

{
  transform: translateX(150px);
  opacity: 0;
}

@keyframes payBox-in {
  0% {
    opacity: 0%;
  }
  10% {
    opacity: 10%;
  }
  20% {
    opacity: 20%;
  }
  50% {
    opacity: 50%;
  }
  100% {
    opacity: 100%;
  }
}

</style>



